import { Box, Button, TextField } from "@material-ui/core";
import React from "react";

const Login = () => {
  return (
    <div>
        <Box
            display='flex'
            alignItems='center'
            justifyContent='center'
            height='100vh'
            flexDirection='column'
        >
            <Box
                fontSize='400%'
                letterSpacing='10px'
                fontStyle='italic'
            >Lessons</Box>
            <Box 
            padding={2}
            width='25%'
            height='25%'
            >
                <Box>
                    <TextField 
                        label='username'
                        autoFocus
                        required 
                        fullWidth
                    />
                </Box>
                <Box>
                    <TextField 
                        label='password' 
                        type='password'
                        required
                        fullWidth
                    />
                </Box>
                <Button variant='contained' >Log in</Button>
            </Box>
        </Box>
    </div>
    
    
  );
}

export default Login;
